import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { CookieService } from 'ngx-cookie-service';
import { DateModelComponent } from '../date-model/date-model.component';
import moment from 'moment';


@Component({
  selector: 'app-dash-broad-cast',
  templateUrl: './dash-broad-cast.component.html',
  styleUrls: ['./dash-broad-cast.component.scss']
})
export class DashBroadCastComponent implements OnInit {
  broadCastList = [];

  constructor(public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService
  ) { }


  calenderPopUp(dateType) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.announcementList(result,'filter');
      }
    });
  }

  announcementList(entity,type) {
    this.dashboardService.getDashlistBroadcastsentbyApartmentId(entity).subscribe((res:any)=>{
      if(type == 'filter' && res.length > 0) {
        this.broadCastList = res.reverse();
      } else {
        this.broadCastList = res.reverse();
        this.broadCastList = this.broadCastList.slice(0,10)
      }
    });
  }

  ngOnInit(): void {
    let entity = {
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
      fromDate : moment(new Date()).subtract(1, 'month').utc().format(),
      toDate : new Date().toISOString()
    }
    this.announcementList(entity,'initial');
  }

  content(dom,type) {
    if(type == 'hover') {
      dom.querySelectorAll('p')[0].style.display = 'none';
      dom.querySelectorAll('p')[1].style.display = 'block';
    } else {
      dom.querySelectorAll('p')[0].style.display = 'block';
      dom.querySelectorAll('p')[1].style.display = 'none';
    }
  }

}
