import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dash-pie-chart',
  templateUrl: './dash-pie-chart.component.html',
  styleUrls: ['./dash-pie-chart.component.scss']
})
export class DashPieChartComponent implements OnInit {
  data = [{ "cliente": "Kowshick", "total": "10" }, { "cliente": "Murugan", "total": "20" }, { "cliente": "Raavan", "total": "30" }, { "cliente": "Recris", "total": "5" }];
  source: any = {
    datatype: 'json',
    datafields: [
      { name: 'cliente', type: 'string' },
      { name: 'total', type: 'number' }],
    localdata: this.data,
    async: false
  };
  dataAdapter: any = new jqx.dataAdapter(this.source, {
    async: false, 
    autoBind: true, 
    loadError: (xhr: any, status: any, error: any) => {
               alert('Error loading "' + this.source.url + '" : ' + error); 
    } 
  });
  legendPosition: any = { left: 520, top: 140, width: 100, height: 100 };
  padding: any = { left: 5, top: 5, right: 5, bottom: 5 };
  titlePadding: any = { left: 0, top: 0, right: 0, bottom: 10 };
  seriesGroups: any[] = [{
    type: 'donut',
    showLabels: true,
    series:[{
    dataField: 'total',
    displayText: 'cliente',
    labelRadius: 120,
    initialAngle: 15,
    radius: 90,
    innerRadius: 40,
    centerOffset: 0,
    formatSettings: { sufix: '%', decimalPlaces: 1 }}]
  }];

  constructor() { }

  getWidth() : any {
		if (document.body.offsetWidth < 850) {
			return '100%';
		}
		return 400;
  }
  
  ngOnInit(): void {
  }

}
