import { Component, OnInit, Input } from '@angular/core';
import { DateModelComponent } from '../date-model/date-model.component';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { CookieService } from 'ngx-cookie-service';
import moment from 'moment';

@Component({
  selector: 'app-admin-dash-second-row',
  templateUrl: './admin-dash-second-row.component.html',
  styleUrls: ['./admin-dash-second-row.component.scss']
})
export class AdminDashSecondRowComponent implements OnInit {
  @Input() label:any; 
  constructor( public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService) { }

  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  calenderPopUp(name,type) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: type
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        if(name == 'Open Community') {
          this.openCommunity(result);
        } else if(name == 'Open Personal') {
          this.openPersonal(result);
        } else if(name == 'High Priority Community') {
          this.highPriorityCommunity(result);
        } else if (name == 'High Priority Personal') {
          this.highPriorityPersonal(result);
        } else if (name == 'Unassigned Community') {
          this.unAssignedCommunity(result);
        } else if (name == 'Unassigned Personal') {
          this.unassignedPersonal(result);
        } else if (name == 'Open Community Aged') {
          this.openCommunityAged(result);
        } else if (name == 'Open Personal Aged') {
          this.openPersonalAged(result);
        } else if (name == 'InProgress Panic Alerts') {
          this.inProgressPanicAlerts(result);
        } else if (name == 'Open Panic Alerts') {
          this.openPanicAlerts(result);
        } else if (name == 'Visitors In') {
          this.visitorsIn(result);
        }
      }
    });
  }

  openCommunity(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashOpencommticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.label.frontValue = res.openCommunitytickets;
    })
  }
  openPersonal(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashOpenpersonalticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.label.backValue = res.openPersonalticket;
    })
  }

  highPriorityCommunity(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashHighpriorityopenCommunityTicketsbyApartmentId(entity).subscribe((res:any)=>{
      this.label.frontValue = res.highPriorityOpenCommunityTicket;
    })
  }
  highPriorityPersonal(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashHighpriorityopenpersonalTicketsbyApartmentId(entity).subscribe((res:any)=>{
      this.label.backValue = res.highPriorityOpenPersonalTicket;
    })
  }

  unAssignedCommunity(result) {
    let params = {
      fromDate : result.date,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashUnassignedCommunityTicketsbyApartmentId(params).subscribe((res:any)=>{
      this.label.frontValue = res.unassignedCommunityTickets;     
    })
  }
  unassignedPersonal(result) {
    let params = {
      fromDate : result.date,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashUnassignedCommunityTicketsbyApartmentId(params).subscribe((res:any)=>{
      this.label.frontValue = res.unassignedCommunityTickets;     
    })
  }

  openCommunityAged(result) {
    let params = {
      date : result.date,
      noOfDays : result.noOfDays,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashOpenCommunityAgedTicketsbyApartmentIDbyDays(params).subscribe((res:any)=>{
      this.label.frontValue = res.openCommunityAgedTickets;     
    })
  }
  openPersonalAged(result) {
    let params = {
      date : result.date,
      noOfDays : result.noOfDays,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashOpenPersonalAgedTicketsbyApartmentIDbyDays(params).subscribe((res:any)=>{
      this.label.backValue = res.openPersonalAgedTickets;
    })
  }

  inProgressPanicAlerts(result) {
    let params = {
      fromDate : result.date,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashinProgressPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.label.frontValue = res.inProgressPanicAlerts;
    })
  }
  openPanicAlerts(result) {
    let params = {
      fromDate : result.date,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashOpenPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.label.backValue = res.openPanicAlerts;
    })
  } 

  visitorsIn(result){
    let params = {
      startDate : result.fromDate,
      EndDate : result.toDate,
      apartmentId : result.apartmentId
    }
    this.dashboardService. getDashVisitorsCheckedinbyApartmentId(params).subscribe((res:any)=>{
      this.label.frontValue = res.visitorsCheckIn;
    });
  }



  yesterday(type) {
    let entity:any = {};
    if(type== 'unassigned' || type== 'openaged' ||  type== 'panicAlerts') {
      entity.date = moment(new Date()).subtract(1, 'days').utc().format();
    } else {
      entity.fromDate = moment(new Date()).subtract(1, 'days').utc().format();
      entity.toDate = new Date().toISOString();
    }
    return entity;
  }

  today(type) {
    let entity:any = {};
    if(type== 'unassigned' || type== 'openaged' || type== 'panicAlerts') {
      entity.date = new Date().toISOString();
    } else {
      entity.fromDate = new Date().toISOString();
      entity.toDate = new Date().toISOString();
    }
    return entity;
  }

  filter(filterMode,name,type) {
    let result = filterMode  == 'yesterday' ? this.yesterday(type) : this.today(type);
    if(name == 'Open Community') {
      this.openCommunity(result);
    } else if(name == 'Open Personal') {
      this.openPersonal(result);
    } else if(name == 'High Priority Community') {
      this.highPriorityCommunity(result);
    } else if (name == 'High Priority Personal') {
      this.highPriorityPersonal(result);
    } else if (name == 'Unassigned Community') {
      this.unAssignedCommunity(result);
    } else if (name == 'Unassigned Personal') {
      this.unassignedPersonal(result);
    } else if (name == 'Open Community Aged') {
      this.openCommunityAged(result)
    } else if (name == 'Open Personal Aged') {
      this.openPersonalAged(result)
    } else if (name == 'InProgress Panic Alerts') {
      this.inProgressPanicAlerts(result);
    } else if (name == 'Open Panic Alerts') {
      this.openPanicAlerts(result);
    } else if (name == 'Visitors In') {
      this.visitorsIn(result);
    }
  }

  ngOnInit(): void {
  }
}
