import { Component, OnInit } from '@angular/core';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { MatDialog } from '@angular/material/dialog';
import { DateModelComponent } from '../date-model/date-model.component';
import { CookieService } from 'ngx-cookie-service';
import moment from 'moment';

@Component({
  selector: 'app-dash-meeting',
  templateUrl: './dash-meeting.component.html',
  styleUrls: ['./dash-meeting.component.scss']
})
export class DashMeetingComponent implements OnInit {
  meetingList = [];

  constructor( public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService) { }

  calenderPopUp(dateType) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.scheduledMeeting(result);
      }
    });
  }

  scheduledMeeting(entity) {
    this.dashboardService.getDashlistupcomingmeetingbyApartmentId(entity).subscribe((res:any)=>{
      this.meetingList = res;
    });
  }

  ngOnInit(): void {
    let entity = {
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
      fromDate : new Date().toISOString(),
      toDate : moment(new Date()).add(1, 'month').utc().format(),
    }
    this.scheduledMeeting(entity);
  }

}
