import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/api/controllers/User';
import { CookieService } from 'ngx-cookie-service';
import { MatDialog } from '@angular/material/dialog';
import { SharedService } from 'src/app/shared/services/shared.service';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { DateModelComponent } from '../../shared/date-model/date-model.component';
@Component({
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.scss']
})
export class AdminDashboardComponent implements OnInit {
  secondRowData:any = [{
    front : 'Open Community',
    back : 'Open Personal',
    frontFooter: true,
    backFooter: true,
    frontValue: '',
    backValue: '',
    type: 'ticket',
    row: 'blue'
    },{
      front : 'High Priority Community',
      back : 'High Priority Personal',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'highPriority',
      row: 'blue'
    },{ 
      front : 'Unassigned Community',
      back : 'Unassigned Personal',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'unassigned',
      row: 'blue'
    },
    {
      front : 'Open Community Aged',
      back : 'Open Personal Aged',
      frontValue: '',
      backValue: '',
      type: 'openaged',
      row: 'blue',
      frontFooter: true,
      backFooter: true
    }
  ];
  fourthRowData:any = [{
    front : 'InProgress Panic Alerts',
    back : 'Open Panic Alerts',
    frontValue: '',
    backValue: '',
    frontFooter: true,
    backFooter: true,
    type: 'panicAlerts',
    row: 'white',
    },{
      front : 'Total Customer',
      frontValue: '',
      row: 'white',
      side: 'one'
    },{ 
      front : 'Total Customer',
      frontValue: '',
      row: 'white',
      side: 'one'
    },
    {
      front : 'Total Credit Note',
      frontValue: '',
      row: 'white',
      side: 'one'
    }
  ]
  sixthRowData:any = [{
    front : 'Visitors In',
    back : 'Visitors Out',
    frontValue: '',
    backValue: '',
    frontFooter: true,
    backFooter: false,
    type: 'visitors',
    row: 'white',
    },{
      front : 'Visitors Not Checked',
      frontValue: '',
      row: 'white',
      side: 'one'
    },{ 
      front : 'Total Parking Slots',
      back : 'Unassigned Parking',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: '',
      row: 'white',
    },{
      front : 'Facility Booking Approved',
      back : 'Facility Booking UnApproved',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: '',
      row: 'white',
    }
  ]
  userRole:string = "";

  constructor(
    private userService:UserService,
  	private sharedService:SharedService,
    private cookieService:CookieService,
    private dashboardService:DashBoardService,
    public dialog: MatDialog
  ) { }

  isAdmin(){
    return this.userRole == "Admin" ? true : false;
  }
  isSecurity(){
    return this.userRole == "Security" ? true : false;
  }
  isStaff(){
    return this.userRole == "Staff" ? true : false;
  }

  secondRow() {
    let entity = {
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
      fromDate : new Date().toISOString(),
      toDate : new Date().toISOString()
    }
    let params = {
      fromDate : new Date().toISOString(),
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    let data = {
      date : new Date().toISOString(),
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashOpencommticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.secondRowData[0].frontValue = res.openCommunitytickets;
    });
    this.dashboardService.getDashOpenpersonalticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.secondRowData[0].backValue = res.openPersonalticket;
    });

    this.dashboardService.getDashHighpriorityopenCommunityTicketsbyApartmentId(entity).subscribe((res:any)=>{
      this.secondRowData[1].frontValue = res.highPriorityOpenCommunityTicket;
    });
    this.dashboardService.getDashHighpriorityopenpersonalTicketsbyApartmentId(entity).subscribe((res:any)=>{
      this.secondRowData[1].backValue = res.highPriorityOpenPersonalTicket;
    });
    this.dashboardService.getDashUnassignedCommunityTicketsbyApartmentId(params).subscribe((res:any)=>{
      this.secondRowData[2].frontValue = res.unassignedCommunityTickets;     
    });
    this.dashboardService.getDashUnassignedpersonalTicketsbyApartmentId(params).subscribe((res:any)=>{
      this.secondRowData[2].backValue = res.unassignedPersonalTickets;
    })
  
    this.dashboardService.getDashOpenCommunityAgedTicketsbyApartmentIDbyDays(data).subscribe((res:any)=>{
      this.secondRowData[3].frontValue = res.openCommunityAgedTickets;     
    })

    this.dashboardService.getDashOpenPersonalAgedTicketsbyApartmentIDbyDays(data).subscribe((res:any)=>{
      this.secondRowData[3].backValue = res.openPersonalAgedTickets;
    })
  }

  fourthRow() {
    let params = {
      fromDate : new Date().toISOString(),
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashinProgressPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.fourthRowData[0].frontValue =  res.inProgressPanicAlerts;
    });
    this.dashboardService.getDashOpenPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.fourthRowData[0].backValue =  res.openPanicAlerts;
    });
    let entity = {
      apartmentId :  parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashpaymentdefaultersasoftodayybyApartmentId(entity).subscribe((res:any)=>{
      this.fourthRowData[1].frontValue =  res.returnDefautlers;
    });
    this.dashboardService.getDashcreditnoteappliedbyApartmentId(entity).subscribe((res:any)=>{
      this.fourthRowData[3].frontValue =  res.creditNotesCount;
    });
  }

  sixthRow() {
    let params = {
      startDate : new Date().toISOString(),
      EndDate : new Date().toISOString(),
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    let entity = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService. getDashVisitorsCheckedinbyApartmentId(params).subscribe((res:any)=>{
      this.sixthRowData[0].frontValue  = res.visitorsCheckIn;
    });
    this.dashboardService. getDashVisitorsCheckedouttodaybyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[0].backValue  = res.visitorsCheckedouttoday;
    });
    this.dashboardService. getDashVisitorsNotcheckedtodaybyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[1].frontValue  = res.visitorsNotcheckedtoday;
    });
    this.dashboardService. getDashuptotalparkingslotsbyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[2].frontValue = res.totalParkingSlots;
    });
    this.dashboardService. getDashuptotalparkingslotsunassignedbyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[2].backValue = res.unassignedParkingSlots;
    });
    this.dashboardService. getDashufacilitybookingapprovedtodaybyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[3].frontValue = res.approvedfaclitiesTodayCount;
    });
    this.dashboardService. getDashufacilitybookingunapprovedbyApartmentId(entity).subscribe((res:any)=>{
      this.sixthRowData[3].backValue = res.unapprovedfaclities;
    });
  }

  ngOnInit() {
    this.userRole = this.cookieService.get('userRoleType');
    this.secondRow();
    this.fourthRow();
    this.sixthRow();
  }

}
