import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { UserService } from 'src/app/api/controllers/User';
import { CookieService } from 'ngx-cookie-service';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  isApartmentSelected:boolean = false;
  userRole:string = "";
  userName:any;
  customDashBoard:boolean = false;
  dashBoardName:string ='General';
  constructor(private userService:UserService,
    private cookieService:CookieService,
  	private sharedService:SharedService,
  ) { }
  
  isAdmin(){
    return this.userRole == "Admin" ? true : false;
  }
  isSecurity(){
    return this.userRole == "Security" ? true : false;
  }
  isStaff(){
    return this.userRole == "Staff" ? true : false;
  }

  dashboardChange(event) {
    if(event.checked) {
      this.customDashBoard = true;
      this.dashBoardName = 'Custom';
    } else {
      this.customDashBoard = false;
      this.dashBoardName = 'General';
    }
  }

  ngOnInit() { 
    let params = {
      userid: parseInt(this.cookieService.get('userId'))
    }
    this.userService.getUserById(params).subscribe((res:any) => {
      this.userName = res[0].firstName;
    },
    error => {
      console.log(error);
    });
  	this.sharedService.apartmentselectedcast.subscribe( isSelected => {
  		this.isApartmentSelected = isSelected;
  	});
  }

}
