import { Component, OnInit } from '@angular/core';
import { DateModelComponent } from '../date-model/date-model.component';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { CookieService } from 'ngx-cookie-service';
import moment from 'moment';

@Component({
  selector: 'app-admin-dash-first-row',
  templateUrl: './admin-dash-first-row.component.html',
  styleUrls: ['./admin-dash-first-row.component.scss']
})
export class AdminDashFirstRowComponent implements OnInit {
  widgets:any = [{
    front : 'Total Units',
    back : 'Total Towers',
    frontValue: '_',
    backValue: '',
    frontFooter: false,
    backFooter: false,
    type: ''
    },{
      front : 'Total Owners',
      middle : 'Total Tenants',
      back :  'Total Admins',
      frontValue: '',
      middleValue : '',
      backValue: '',
      frontFooter: false,
      middleFooter: false,
      backFooter: false,
      type: '',
    },{
      front : 'Pending Admins',
      back : 'Pending Users',
      frontValue: '',
      backValue: '',
      frontFooter: true,
      backFooter:true,
      type: 'pending'
    },{
      front : 'Total',
      back : 'Total',
      frontValue: '',
      backValue: '',
      frontFooter: true,
      backFooter:true,
      type: ''
    },{
      front : 'Un Approve Move In',
      back : 'Un Approve Move Out',
      frontValue: '',
      backValue: '',
      frontFooter: true,
      backFooter:true,
      type: 'unapprove'
    },{
      front : 'Up Coming Move In',
      back : 'Up Coming Move Out',
      frontValue: '',
      backValue: '',
      frontFooter: true,
      backFooter:true,
      type: 'upcoming'
    }
  ];
  colType:string = 'owner';

  constructor(
    public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService) { }

  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  pendingAdmins(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashUnapprovedUsersPendingAdminbyApartmentId(entity).subscribe((res:any)=>{
      this.widgets[2].frontValue = res.unapprovedUsersPendingAdmin;
    })
  }

  pendingUsers(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashUnapprovedUsersPendingusersbyApartmentId(entity).subscribe((res:any)=>{
      this.widgets[2].backValue = res.unapprovedPendingusers;
    })
  }

  unApproveMoveIn(result) {
    let entity = {
      date : result.date,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashunapprovedmoveinbyApartmentId(entity).subscribe((res:any)=>{
      this.widgets[4].frontValue = res.unapprovedMoveIn;
    })
  }

  unApproveMoveOut(result) {
    let entity = {
      date : result.date,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashunapprovedmoveoutbyApartmentId(entity).subscribe((res:any)=>{
      this.widgets[4].backValue = res.unapprovedMoveOut;
    })
  }

  upcomingMoveIn(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashupcomingmoveinApartmentId(entity).subscribe((res:any)=>{
      this.widgets[5].frontValue = res.upcomingWeeklyApprovedMoveInCounts;     
    });
  }

  upcomingMoveOut(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : parseInt(this.cookieService.get('apartmentId')),
    }
    this.dashboardService.getDashupcomingmoveoutApprovedbyApartmentId(entity).subscribe((res:any)=>{
      this.widgets[5].backValue = res.upcomingMoveOut;
    });
  }

  yesterday(type) {
    let entity:any = {};
    if(type== 'unapprove') {
      entity.date = moment(new Date()).subtract(1, 'days').utc().format();
    } else {
      entity.fromDate = moment(new Date()).subtract(1, 'days').utc().format();
      entity.toDate = new Date().toISOString();
    }
    return entity;
  }

  today(type) {
    let entity:any = {};
    if(type== 'unapprove') {
      entity.date = new Date().toISOString();
    } else {
      entity.fromDate = new Date().toISOString();
      entity.toDate = new Date().toISOString();
    }
    return entity;
  }

  filter(filterMode,name,type) {
    let result = filterMode  == 'yesterday' ? this.yesterday(type) : this.today(type);
    if(name == 'Pending Admins') {
      this.pendingAdmins(result);
    } else if(name == 'Pending Users') {
      this.pendingUsers(result);
    } else if(name == 'Un Approve Move In') {
      this.unApproveMoveIn(result);
    } else if (name == 'Un Approve Move Out') {
      this.unApproveMoveOut(result);
    } else if (name == 'Up Coming Move In') {
      this.upcomingMoveIn(result);
    }  else if (name == 'Up Coming Move Out') {
      this.upcomingMoveOut(result);
    }
  }

  calenderPopUp(name,type) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: type
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        if(name == 'Pending Admins') {
          this.pendingAdmins(result);
        } else if(name == 'Pending Users') {
          this.pendingUsers(result);
        } else if(name == 'Un Approve Move In') {
          this.unApproveMoveIn(result);
        } else if (name == 'Un Approve Move Out') {
          this.unApproveMoveOut(result);
        } else if (name == 'Up Coming Move In') {
          this.upcomingMoveIn(result);
        }  else if (name == 'Up Coming Move Out') {
          this.upcomingMoveOut(result);
        }
      }
    });
  }

  ngOnInit(): void {
    let params = {
      apartmentId :  parseInt(this.cookieService.get('apartmentId'))
    }
    this.dashboardService.getDashTotalunitsbyApartmentId(params).subscribe((res:any)=>{
      this.widgets[0].frontValue = res.totalUnits;
    });
    this.dashboardService.getDashTotalTowersbyApartmentId(params).subscribe((res:any)=>{
      this.widgets[0].backValue = res.totalTowers;
    });
    this.dashboardService.getDashTotalownersbyApartmentId(params).subscribe((res:any)=>{
      this.widgets[1].frontValue = 50;
    });
    this.dashboardService.getDashTotalTenantsbyApartmentId(params).subscribe((res:any)=>{
      this.widgets[1].middleValue = res.totalTenants;
    });
    this.dashboardService.getDashTotalAdminsbyApartmentId(params).subscribe((res:any)=>{
      this.widgets[1].backValue = res.totalAdmins;
    });
    this.pendingAdmins(this.today('pending'));
    this.pendingUsers(this.today('pending'));
    this.unApproveMoveIn(this.today('unapprove'));
    this.unApproveMoveOut(this.today('unapprove'));
    this.upcomingMoveIn(this.today('upcoming'));
    this.upcomingMoveOut(this.today('upcoming'));
  }
}
